.about-page {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 1.7vh;
}

.content-column {
  width: 35%;
}

.about-page__title {
  flex: 1 1 100%;
  font-weight: 900;
  font-size: 7vh;
  transform: translateX(150vw);

  &::after {
    display: inline-block;
    font-family: $regular-font;
    font-style: italic;
    font-weight: 900;
    font-size: 3.5vh;
    color: #fff;
    opacity: 0.2;
    vertical-align: middle;
  }
}

.swiper-slide-active .about-page__title {
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12);
  transform: translateY(0);
}

.accent {
  color: $prime-color;
  font-weight: 900;
  font-style: italic;
}

.underline {
  text-decoration: underline;
  font-weight: 900;
  font-style: italic;
}

.bold {
  font-weight: 900;
  font-style: italic;
}

.screen__first {
  padding-top: 12%;
}

.screen__second .about-page__title {
  margin-bottom: 4vh;

  &::after {
    content: "Подход";
  }
}

.numbers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-style: italic;
  font-weight: 900;
  font-size: 19px;
}

.big {
  font-size: 7vh;
  line-height: 1.2;
}

.numbers-pad {
  padding-top: 8vh;
}

.screen__third .about-page__title {
  margin-bottom: 7.8vh;

  &::after {
    content: "Мисия";
  }
}

.grey-text {
  margin-top: 5vh;
  opacity: 0.66;
}

.screen__fourth .about-page__title {
  margin-bottom: 3vh;
}

.about-page__list li {
  margin-bottom: 5vh;
  position: relative;
  padding-left: 25px;

  &::before {
    content: "";
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background-color: $prime-color;
    position: absolute;
    top: 7px;
    left: 0;
  }
}

.about-page__footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 2;
  color: #fff;
  padding: 20px 0;

  & .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  & .contacts__item a {
    font-size: 2.5vh;
    margin-bottom: 0;
    line-height: 1.2;

    &::before {
      width: 20px;
      height: 20px;
      margin-right: 15px;
    }
  }
}

.copyright, .confed {
  font-size: 1.5vh;
}

.confed {
  border-bottom: 1px solid;
}


.light {
  font-size: 0.8vh;
  font-weight: 400;
  display: block;
  text-align: right;
}

.about-page__footer .phone,
.about-page__footer .email {
  opacity: 1;
  transform: translateY(-50px);
  transition: transform 0.8s cubic-bezier(0.1, 0.39, 0, 1.12);
}

.about-page__footer .phone a::before {
    background-image: url('../img/call.svg');
}

.about-page__footer .email a::before {
    background-image: url('../img/ios-mail.svg');
}

@media (max-width: 1050px) {
  .about-page__footer .wrapper {
    flex-wrap: wrap;
    justify-content: space-evenly;
    text-align: center;
  }

  .about-page__footer .copyright {
    order: 3;
  }

  .about-page__footer .phone,
  .about-page__footer .email {
    transform: translateY(0) !important;
    margin-bottom: 10px;
  }
}

@media (max-width: 900px) {
  .content-column {
    width: 48%;
  }

  .header__logo-img {
    transform-origin: left top;
  }
}

@media (max-width: 650px) {
  .content-column {
    width: 100%;
    margin-bottom: 10px;
  }

  .about-page__title::after {
    display: none;
  }

  .numbers {
    font-size: 17px;
    margin-top: 10px;
  }

  .big {
    font-size: 28px;
  }

  .numbers-pad {
    padding-top: 0;
  }

  .about-page {
    font-size: 16px;
  }

  .screen__second .about-page__title,
  .screen__third .about-page__title,
  .screen__fourth .about-page__title {
    font-size: 36px;
    margin-bottom: 20px;
  }

  .about-page__list li {
    margin-bottom: 10px;
  }

  .grey-text {
    margin-top: 10px;
  }
}

@media (max-height: 500px) {
  .about-page {
    font-size: 12px;
  }

  .screen__second .about-page__title,
  .screen__third .about-page__title,
  .screen__fourth .about-page__title {
    font-size: 30px;
    margin-bottom: 20px;
  }

  .about-page__list li {
    margin-bottom: 10px;
  }

  .grey-text {
    margin-top: 10px;
  }

  .screen__first {
    padding-top: 6%;
  }

  .confed, .copyright {
    font-size: 10px;
  }

  .about-page__footer .contacts__item a {
    font-size: 14px;
  }

  .light {
    font-size: 6px;
  }
}
